.chatMain {
  height: 100vh;
  display: flex;
  width: 100%;
}

.chatMenu {
  flex: 3.5;
}

.chatInput {
  width: 60%;
  padding: 10px 0;
  border: none;
  border-bottom: 1px solid grey;
}

.chatBox {
  flex: 5.5;
}

.chatOnline {
  flex: 3;
}

.chatBoxWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}

.chatBoxTop {
  height: 100%;
  overflow-y: scroll;
  padding-right: 10px;
}

.chatBoxBottom {
  margin-top: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.chatMessageInput {
  width: 80%;
  height: 90px;
  padding: 10px;
}

.chatSubmitButton {
  width: 70px;
  height: 40px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: teal;
  color: white;
}

.chatMenuWrapper,
.chatBoxWrapper,
.chatOnlineWrapper {
  padding: 10px;
  height: 100%;
  box-sizing: border-box;
}

.noConversationText {
  position: absolute;
  top: 30%;
  font-size: 30px;
  color: #333333;
  cursor: default;
}
